import React from "react";
import "./css/style.css";

function Image(props){
    return (
        <div key={props.url} className="click-item col-3" onClick={() => props.handleOnClick(props.index)}>
            <img src={props.url} aria-label="click item" alt="reborn" className="img-thumbnail"></img>
        </div>
    );
}

export default Image;